* {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  /* font-family: 'Nunito Sans', sans-serif; */
  box-sizing: border-box;
}

/* MOBILE: setting 1rem = 8px (8px / 16px) */
html {
  font-size: 50%;
}

/*  TABLET: setting 1rem = 9px (9px / 16px) */
@media screen and (min-width: 768px) {
  html {
    font-size: 56.25%;
  }
}

/* DESKTOP: setting 1rem = 10px */
@media screen and (min-width: 1028px) {
  html {
    font-size: 62.5%;
  }
}

body {
  font-size: 1.6rem;
  /* color: $dark-gray; */
}
